// Configuration
// --------------------------------------------------

@import 'config';

// Mixins
// --------------------------------------------------
@import '_mixins/_aspect-ratio.scss';
@import '_mixins/_bem.scss';
@import '_mixins/_breakpoints.scss';
@import '_mixins/_clearfix.scss';
@import '_mixins/_drop-shadow.scss';
@import '_mixins/_grid_framework.scss';
@import '_mixins/_grid.scss';
@import '_mixins/_reset.scss';
@import '_mixins/_shadow.scss';
@import '_mixins/_valign.scss';

// Redactor
// --------------------------------------------------

html body .redactor-editor {
    @import '_core/reboot';
    @import '_core/typography';
    @import '_core/grid';
    @import '_core/form';
    @import '_core/functions';
    @import '_core/print';
    @import '_core/debug';

    @import 'components/_button';
    @import 'components/_interior-boxes.scss';
    @import 'components/_table';

    // Utilities

    //
    // Alignment
    // --------------------------------------------------

    .u-left {
        text-align: left;
    }

    .u-centered {
        text-align: center;
    }

    .u-right {
        text-align: right;
    }

    .u-vertical-align {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
    }

    .u-flex-align {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    //
    // Float Alignment
    // --------------------------------------------------

    .u-pull-left {
        float: left;
    }

    .u-pull-right {
        float: right;
    }

    //
    // Clearing
    // --------------------------------------------------

    .u-clearing {
        @include clearfix();
    }

    //
    // Pre-defined Heights
    // --------------------------------------------------

    .u-height-100 {
        min-height: 100px !important;
    }

    .u-height-150 {
        min-height: 150px !important;
    }

    .u-height-200 {
        min-height: 200px !important;
    }

    .u-height-300 {
        min-height: 300px !important;
    }

    .u-height-400 {
        min-height: 400px !important;
    }

    .u-height-500 {
        min-height: 500px !important;
    }

    //
    // Typography
    // --------------------------------------------------

    .u-italic {
        font-style: italic;
    }

    .u-text-center {
        text-align: center;
    }

    .u-thin {
        font-weight: $font-weight-thin;
    }

    //
    // Layout
    // --------------------------------------------------

    .u-wrapper {
        position: relative;
    }

    //
    // Display
    // --------------------------------------------------

    .u-is-hidden {
        display: none;
    }

    //
    // Borders
    // --------------------------------------------------

    .u-border-top {
        border-top: 1px solid #d8d8d8;
    }

    .u-border-right {
        border-right: 1px solid #d8d8d8;
    }

    .u-border-bottom {
        border-bottom: 1px solid #d8d8d8;
    }

    .u-border-left {
        border-left: 1px solid #d8d8d8;
    }

    //
    // Spacing
    // --------------------------------------------------

    // Margin

    .u-m-a-0 { margin:0 !important; }
    .u-m-t-0 { margin-top: 0 !important; }
    .u-m-r-0 { margin-right: 0 !important; }
    .u-m-b-0 { margin-bottom: 0 !important; }
    .u-m-l-0 { margin-left: 0 !important; }
    .u-m-x-0 { margin-right: 0 !important; margin-left: 0 !important; }
    .u-m-y-0 { margin-top: 0 !important; margin-bottom: 0 !important; }

    .u-m-a { margin:$spacer !important; }
    .u-m-t { margin-top: $spacer-y !important; }
    .u-m-r { margin-right: $spacer-x !important; }
    .u-m-b { margin-bottom: $spacer-y !important; }
    .u-m-l { margin-left: $spacer-x !important; }
    .u-m-x { margin-right: $spacer-x !important; margin-left: $spacer-x !important; }
    .u-m-y { margin-top: $spacer-y !important; margin-bottom: $spacer-y !important; }
    .u-m-x-auto { margin-right: auto !important; margin-left: auto !important; }

    .u-m-a-md { margin:($spacer * 1.5) !important; }
    .u-m-t-md { margin-top: ($spacer-y * 1.5) !important; }
    .u-m-r-md { margin-right: ($spacer-y * 1.5) !important; }
    .u-m-b-md { margin-bottom: ($spacer-y * 1.5) !important; }
    .u-m-l-md { margin-left: ($spacer-y * 1.5) !important; }
    .u-m-x-md { margin-right: ($spacer-x * 1.5) !important; margin-left: ($spacer-x * 1.5) !important; }
    .u-m-y-md { margin-top: ($spacer-y * 1.5) !important; margin-bottom: ($spacer-y * 1.5) !important; }

    .u-m-a-lg { margin:($spacer * 3) !important; }
    .u-m-t-lg { margin-top: ($spacer-y * 3) !important; }
    .u-m-r-lg { margin-right: ($spacer-y * 3) !important; }
    .u-m-b-lg { margin-bottom: ($spacer-y * 3) !important; }
    .u-m-l-lg { margin-left: ($spacer-y * 3) !important; }
    .u-m-x-lg { margin-right: ($spacer-x * 3) !important; margin-left: ($spacer-x * 3) !important; }
    .u-m-y-lg { margin-top: ($spacer-y * 3) !important; margin-bottom: ($spacer-y * 3) !important; }

    // Padding

    .u-p-a-0 { padding:0 !important; }
    .u-p-t-0 { padding-top: 0 !important; }
    .u-p-r-0 { padding-right: 0 !important; }
    .u-p-b-0 { padding-bottom: 0 !important; }
    .u-p-l-0 { padding-left: 0 !important; }
    .u-p-x-0 { padding-right: 0 !important; padding-left: 0 !important; }
    .u-p-y-0 { padding-top: 0 !important; padding-bottom: 0 !important; }

    .u-p-a { padding:$spacer !important; }
    .u-p-t { padding-top: $spacer-y !important; }
    .u-p-r { padding-right: $spacer-x !important; }
    .u-p-b { padding-bottom: $spacer-y !important; }
    .u-p-l { padding-left: $spacer-x !important; }
    .u-p-x { padding-right: $spacer-x !important; padding-left: $spacer-x !important; }
    .u-p-y { padding-top: $spacer-y !important; padding-bottom: $spacer-y !important; }

    .u-p-a-md { padding:($spacer * 1.5) !important; }
    .u-p-t-md { padding-top: ($spacer-y * 1.5) !important; }
    .u-p-r-md { padding-right: ($spacer-y * 1.5) !important; }
    .u-p-b-md { padding-bottom: ($spacer-y * 1.5) !important; }
    .u-p-l-md { padding-left: ($spacer-y * 1.5) !important; }
    .u-p-x-md { padding-right: ($spacer-x * 1.5) !important; padding-left: ($spacer-x * 1.5) !important; }
    .u-p-y-md { padding-top: ($spacer-y * 1.5) !important; padding-bottom: ($spacer-y * 1.5) !important; }

    .u-p-a-lg { padding:($spacer * 3) !important; }
    .u-p-t-lg { padding-top: ($spacer-y * 3) !important; }
    .u-p-r-lg { padding-right: ($spacer-y * 3) !important; }
    .u-p-b-lg { padding-bottom: ($spacer-y * 3) !important; }
    .u-p-l-lg { padding-left: ($spacer-y * 3) !important; }
    .u-p-x-lg { padding-right: ($spacer-x * 3) !important; padding-left: ($spacer-x * 3) !important; }
    .u-p-y-lg { padding-top: ($spacer-y * 3) !important; padding-bottom: ($spacer-y * 3) !important; }
    //
    // Prevent Scrolling
    // --------------------------------------------------

    .u-locked {
        overflow: hidden;
    }

    //
    // Responsive utilities
    // --------------------------------------------------

    @each $bp in map-keys($grid-breakpoints) {
        .u-hidden-#{$bp}-up {
            @include media-breakpoint-up($bp) {
                display: none !important;
            }
        }
        .u-hidden-#{$bp}-down {
            @include media-breakpoint-down($bp) {
                display: none !important;
            }
        }
    }

    // Print utilities
    //
    // Media queries are placed on the inside to be mixin-friendly.

    .u-visible-print-block {
        display: none !important;

        @media print {
            display: block !important;
        }
    }

    .u-visible-print-inline {
        display: none !important;

        @media print {
            display: inline !important;
        }
    }

    .u-visible-print-inline-block {
        display: none !important;

        @media print {
            display: inline-block !important;
        }
    }

    .u-hidden-print {
        @media print {
            display: none !important;
        }
    }

    ul {
        list-style-type: square;
        list-style-position: outside;

        > li {
            margin-left: 20px;
            margin-bottom: 5px;
        }
    }

    ol {

        > li {
            margin-left: 20px;
            margin-bottom: 5px;
        }
    }

    h1,
    h2,
    h3 {
        font-family: $font-family-serif;
        font-weight: $font-weight-normal;
        line-height: $line-height - .2;
        border-bottom: 1px solid #dedede;
        padding-bottom: 20px;
        margin-bottom: 20px;
    }

    h2,h3 {
        margin-top: 40px;
    }

    .callout {
        margin: 30px 0;
        padding: 20px 30px;
        font-size: $font-size-lg;
        line-height: $line-height - .2;
        font-style: italic;
        font-weight: 400;
        background-color: #fff;
        border: 4px double #dedede;
    }
}

// Redactor Additional Formatting
// --------------------------------------------------

.redactor-dropdown .redactor-dropdown-callout {

    span {
        width: 90%;
    }

    &:hover span span,
    span span {
        padding: 2px 10px;

        padding: 10px 15px;
        font-size: 1.25rem;
        line-height: 1.3;
        font-style: italic;
        font-weight: 400;
        background-color: #fff;
        border: 4px double #dedede;
    }
}

// Misc
// --------------------------------------------------

.btn.icon,
a.icon {
    font: inherit;
    font-family: HelveticaNeue, sans-serif;
    font-size: 13px;
    -webkit-font-smoothing: auto;
}
